.rangeWrapper input {
  width:300px;
  height: 20px
}
input[type='range'] {
	appearance: none;
	height: 7px;
	background: grey;
	border-radius: 5px;
	background-image: linear-gradient(#E78EA9, #E78EA9);
	background-repeat: no-repeat;
} 

input[type='range']::-webkit-slider-thumb {
	appearance: none;
	height: 20px;
	width: 20px;
	border-radius: 50%;
	background: #E78EA9;
	cursor: pointer;
	box-shadow: 0 0 2px 0 #555;
} 

input[type='range']::-webkit-slider-runnable-track {
	appearance: none;
	box-shadow: none;
	border: none;
	background: transparent;
}

input[type="range"]::-webkit-slider-thumb:hover {
  box-shadow: #d46a6a50 0px 0px 0px 8px;
}

input[type="range"]::-webkit-slider-thumb:active {
  box-shadow: #d46a6a50 0px 0px 0px 11px;
  transition: box-shadow 350ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, left 350ms cubic-
  bezier(0.4, 0, 0.2, 1) 0ms, bottom 350ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}
